body {
    margin: 0;
    padding: 0;
    background-color: #111;
    color: #fff;
    display: flex;
    flex-direction: column;
}

.game-container {
    padding-bottom: 100px;
    width: 100%;
    max-width: 1000px;
    margin: 0 auto;
    text-align: center;
    padding-top: 100px;
    color: #fff;
}

.gallery {
    display: grid;
    grid-template-columns: repeat(2, 256px);
    gap: 20px;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    margin: 0 auto;
    width: 100%;
    max-width: 512px;
}

.image-item {
    width: 256px;
    height: 256px;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 255, 128, 0.2);
    padding: 10px;
    text-align: center;
    cursor: pointer;
}

.image-item img {
    width: 100%;
    height: 100%;
    border-radius: 8px;
    object-fit: cover;
}

.back-arrow-game {
    position: fixed;
    top: 50px;
    left: 1rem;
    color: #4CAF50;
    font-size: 2rem;
    text-decoration: none;
    cursor: pointer;
    z-index: 1000;
    transition: color 0.3s ease;
}

.back-arrow-game:hover {
    color: #81C784;
}

/*Score*/
.score-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    background: rgba(0, 0, 0, 0.5);
    border-radius: 10px;
    margin-bottom: 1rem;
    box-shadow: 0 0 10px rgba(0, 255, 128, 0.3);
}

.score-container h2 {
    font-size: 1.2rem;
    color: #ccc;
}

.score-container span {
    color: #4CAF50;
}


@media (max-width: 600px) {
    .gallery {
        grid-template-columns: repeat(2, 150px);
        gap: 23px;
        max-width: 300px;
    }

    .image-item {
        width: 160px;
        height: 160px;
    }

    .back-arrow-game {
        top: 3.5rem;
        left: 0.4rem;
    }
}
