.back-arrow-learning {
    position: fixed;
    top: 20px;
    left: 20px;
    color: #4CAF50;
    font-size: 2rem;
    text-decoration: none;
    cursor: pointer;
    z-index: 1000;
    transition: color 0.3s ease;
}

.back-arrow-learning:hover {
    color: #81C784;
}


@media (max-width: 756px) {
    .container {
        max-width: 90%;
    }
}

@media (max-width: 800px) {
    h1, h2 {
        color: #4CAF50;
        font-size: 0.9rem;
        letter-spacing: -1px;
        word-spacing: -15px;
        line-height: 1.5;
    }
    p {
        font-size: 1rem;
        line-height: 1.4;
        letter-spacing: -2px;
        word-spacing: -1px;
    }
    button {
        font-size: 0.9rem;
        letter-spacing: -0.3px;
    }
}